import {
	Grid,
	Table,
	TableBody,
	TableContainer,
	Alert,
	useTheme,
	InputLabel,
	Button,
	Box,
	CardMedia,
	Fab,
	Stack,
} from '@material-ui/core';
import _get from 'lodash.get';
import _isEmpty from 'lodash.isempty';
import { useQuery, gql } from '@apollo/client';
import CloudUploadIcon from '@material-ui/icons/CloudUpload';
import CloseIcon from '@material-ui/icons/Close';
import { makeStyles } from '@material-ui/styles';
import ImageUploading, { ImageListType } from 'react-images-uploading';
import { useEffect, useState } from 'react';
import clsx from 'clsx';
import AnimateButton from '../../../ui-component/extended/AnimateButton';

import SubCard from '../../../ui-component/cards/SubCard';
import { gridSpacing } from '../../../store/constant';
import { TOffer } from '../../../types';
import { getOffer } from '../../../graphql/queries';
import Loader from '../../../ui-component/Loader';
import { getOfferPrice } from '../../../helpers/offer';
import TextRow from '../components/TextRow';

const useStyles = makeStyles(() => ({
	fileInput: {
		background: '#fcfcfe',
		width: '100%',
		marginBottom: '24px',
		'&:dragover, &:dragenter': {
			background: '#333',
		},
	},
	fileContainer: {
		padding: '30px 0',
		textAlign: 'center',
		borderRadius: '4px',
		cursor: 'pointer',
		'& > svg': {
			verticalAlign: 'sub',
			marginRight: '5px',
		},
	},
	uploadImage: {
		position: 'relative',
		overflow: 'hidden',
		borderRadius: '4px',
		cursor: 'pointer',
		width: '100%',
		minHeight: '100px',
		display: 'flex',
		alignItems: 'center',
		justifyContent: 'center',
		'& > svg': {
			verticalAlign: 'sub',
			marginRight: '5px',
		},
		'&:hover $imageHover': {
			opacity: 1,
			zIndex: 1,
		},
	},
	imageHover: {
		position: 'absolute',
		opacity: 0,
		zIndex: -1,
		left: '0',
		top: '0',
		background: 'rgba(255, 255, 255, .8)',
		width: '100%',
		height: '100%',
		display: 'inline-flex',
		transition: 'all 0.3s ease',
	},
	removeButton: {
		margin: 'auto',
	},
}));

type Props = {
	id: string;
};

const OfferEdit = ({ id }: Props) => {
	const { loading, error, data } = useQuery(gql(getOffer), {
		variables: { OfferId: id },
		errorPolicy: 'all',
	});
	const offer = _get(data, 'getOffer', {});
	const offerThumbnail = _get(offer, 'Thumbnail', null);
	const [images, setImages] = useState<Array<{ dataURL: string; file: any }>>(
		[]
	);
	const theme = useTheme();
	const classes = useStyles();

	useEffect(() => {
		if (!offerThumbnail) return;

		const { presignedUrl = '' } = offerThumbnail;
		const thumbnail = {
			dataURL: presignedUrl,
			file: null,
		};
		setImages([thumbnail]);
	}, [offerThumbnail]);

	if (loading) return <Loader />;

	if (error && _isEmpty(offer)) {
		return (
			<Alert severity='error' sx={{ color: theme.palette.error.main }}>
				Offer not found
			</Alert>
		);
	}

	const onChange = (imageList: ImageListType) => {
		setImages(imageList as never[]);
	};

	return (
		<Grid container spacing={gridSpacing}>
			<Grid item lg={6} xs={12}>
				<SubCard title='Offer Information'>
					<TableContainer>
						<Table
							sx={{
								'& td': {
									borderBottom: 'none',
								},
							}}
							size='small'
						>
							<TableBody>
								<TextRow<TOffer> data={offer} source='OfferNo' label='Offer No' />
								<TextRow<TOffer>
									data={offer}
									source='PropertyData.FullAddress'
									label='Property Address'
								/>
								<TextRow<TOffer>
									data={offer}
									source='PropertyData.OwnerNames'
									label='Sellers'
								/>
								<TextRow<TOffer>
									data={offer}
									getValue={getOfferPrice}
									label='Offer Price'
								/>
								<TextRow<TOffer>
									data={offer}
									source='Funding.PurchaseType'
									label='Funding'
								/>
								<TextRow<TOffer>
									data={offer}
									source='Funding.LoanType'
									label='Loan Type'
								/>
								<TextRow<TOffer>
									data={offer}
									source='Funding.DownPayment'
									label='Down Payment'
								/>
								<TextRow<TOffer> data={offer} source='Step.name' label='Status' />
							</TableBody>
						</Table>
					</TableContainer>
				</SubCard>
			</Grid>
			<Grid item lg={6} xs={12}>
				<Grid container direction='column' spacing={gridSpacing}>
					<Grid item xs={12}>
						<SubCard title='Signed Purchase Agreement'>
							<ImageUploading value={images} onChange={onChange}>
								{({ onImageUpload, onImageRemove, dragProps }) => (
									<Box component='div'>
										<Button
											id='file-upload'
											type='button'
											className={clsx(classes.fileInput)}
											onClick={onImageUpload}
											{...dragProps}
										>	
											<InputLabel className={classes.fileContainer}>
												<CloudUploadIcon />
												Drop file here to upload
											</InputLabel>
										</Button>
										<Grid container spacing={1}>
											{images.map((image, index) => (
												<Grid
													item
													xs={12}
													md={6}
													lg={4}
													alignContent='center'
													alignItems='center'
													// eslint-disable-next-line react/no-array-index-key
													key={index}
												>
													<div className={classes.uploadImage}>
														<CardMedia component='img' image={image.dataURL} title='image' />
														<div className={classes.imageHover}>
															<Fab
																color='secondary'
																size='small'
																className={classes.removeButton}
																onClick={() => onImageRemove(index)}
															>
																<CloseIcon />
															</Fab>
														</div>
													</div>
												</Grid>
											))}
										</Grid>
									</Box>
								)}
							</ImageUploading>
						</SubCard>
					</Grid>
					<Grid item xs={12}>
						<SubCard title='SCAR'>
							<ImageUploading value={images} onChange={onChange}>
								{({ onImageUpload, onImageRemove, dragProps }) => (
									<Box component='div'>
										<Button
											id='file-upload'
											type='button'
											className={clsx(classes.fileInput)}
											onClick={onImageUpload}
											{...dragProps}
										>
											<InputLabel className={classes.fileContainer}>
												<CloudUploadIcon />
												Drop file here to upload
											</InputLabel>
										</Button>
										<Grid container spacing={1}>
											{images.map((image, index) => (
												<Grid
													item
													xs={12}
													md={6}
													lg={4}
													alignContent='center'
													alignItems='center'
													// eslint-disable-next-line react/no-array-index-key
													key={index}
												>
													<div className={classes.uploadImage}>
														<CardMedia component='img' image={image.dataURL} title='image' />
														<div className={classes.imageHover}>
															<Fab
																color='secondary'
																size='small'
																className={classes.removeButton}
																onClick={() => onImageRemove(index)}
															>
																<CloseIcon />
															</Fab>
														</div>
													</div>
												</Grid>
											))}
										</Grid>
									</Box>
								)}
							</ImageUploading>
						</SubCard>
					</Grid>
				</Grid>
			</Grid>
			<Grid item xs={12}>
				<Stack direction='row' justifyContent='flex-end'>
					<AnimateButton>
						<Button color='primary' variant='contained' type='submit'>
							Update
						</Button>
					</AnimateButton>
				</Stack>
			</Grid>
		</Grid>
	);
};

export default OfferEdit;
